import React, { Fragment, useCallback, useEffect, useRef, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import 'react-tabs/style/react-tabs.css';
import Form from 'react-validation/build/form';
import { setAuthenticatedUser } from '../../../reduxStore/actions/authActions';
import { deleteUser, setBillable, setBillingProducts, setCompanyData, setShowMenu, setShowSideMenu, setUsersArray } from '../../../reduxStore/actions/flowActions';
import { tutorialModalOpen } from '../../../reduxStore/actions/modalsActions';
import cls from './BillingInfo.module.scss';
import { Button, ModalR, PermissionWrapper, RenderTitle, po } from '../../common';
import { first, take } from 'rxjs/operators';
import { getCompanyDetails } from '../../../services/company.service';
import { loadStripe } from '@stripe/stripe-js';
import { now } from 'moment';
import { getInvoices, getStripePlans, getCurrentSubscription, unsubscribe, getPurchases, getSubscriptionPlans, setActive } from '../../../services/payments.service';
import { useTable } from 'react-table';
import { useNavigate, useParams } from 'react-router-dom';
import moment from 'moment';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import CancelMembership from './CancelMembership';
import MessageContainer from '../ProcessesComps/MessageContainer/MessageContainer';
import { PAYMONGO_KEY } from '../../../services/API_CONSTANTS';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { isMobile } from 'react-device-detect';
import TaskCard from '../TaskComps/TaskCard/TaskCard';
import { APP_NAME, generateNewUUID, ipsum } from '../../../shared/utility';
import { getAllUsers, updateUserById } from '../../../services/user.service';
import UserCard from '../../common/UserCard/UserCard';
import CheckoutPage from '../CheckoutPage/CheckoutPage';

let stripePromise;

const getStripe = () => {
	if (!stripePromise) {
		stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PUBLIC_KEY);
	}

	return stripePromise;
};

function BillingInfo(props) {
	const [stripeError, setStripeError] = useState(null);
	const { companyData, onSetCompanyDataField, companyUsersArray, billable, billProducts, authenticatedUser, } = props;
	const form = useRef();
	const dispatch = useDispatch();
	const [apiError, setApiError] = useState('');
	const [products, setProducts] = useState([]);
	const [prices, setPrices] = useState([]);
	const [loading, setLoading] = useState(false);
	const [invoices, setInvoices] = useState([]);
	const [tabIndex, setTabIndex] = useState(0);
	const [showAll, setShowAll] = useState(false);
	const [amount, setAmount] = useState(-1);
	const [subscriptionInfo, setSubscriptionInfo] = useState({
		end_date: '',
	});
	const [servicesPurchased, setServicesPurchased] = useState([]);
	const [latestDatePurchased, setLatestDatePurchased] = useState(null);
	const [showCancelMembership, setShowCancelMembership] = useState(false);
	const [billableUsers, setBillableUsers] = useState(companyUsersArray.filter(user => new Date(user.validity) < now()));
	const [customBillableUsers, setCustomBillableUsers] = useState([]);
	const { name } = companyData;
	const [subscriptionDone, setSubscriptionDone] = useState(false);
	const { hash } = useParams();

	const [fieldsErrors] = useState({
		name: '',
		founding_date: '',
		employees_no: '',
		industry: '',
		description: '',
		position: '',
		misc_info: '',
	});
	const [, setElementsTouched] = useState({
		name: false,
		founding_date: false,
		employees_no: false,
		industry: false,
		description: false,
		position: false,
	});

	document.title = `Billing | ${APP_NAME}`;

	useEffect(() => {
		if (hash?.length > 0) {
			if (billable?.id.length > 0 && billable?.id === hash) {
				billable.subscriptionInfo.billable_users.filter((user, index) => {
					updateUserById(
						user.id,
						user.first_name,
						user.last_name,
						user.phone,
						null,
						`${moment()
							.add(billable.subscriptionInfo?.duration ?? 30, 'days')
							.format('YYYY-MM-DD HH:mm:ss')}`
					)
						.pipe(first())
						.subscribe({
							next: () => {
								if (index === billable.subscriptionInfo.billable_users.length - 1) {
									setActive({
										id: billable?.id,
										total: billable.subscriptionInfo.total,
										product: billable.subscriptionInfo.plan,
										quantity: 1,
										ends_at: `${moment()
											.add(billable.subscriptionInfo?.duration ?? 30, 'days')
											.format('YYYY-MM-DD HH:mm:ss')}`,
									})
										.pipe(first())
										.subscribe({
											next: () => setSubscriptionDone(true),
										});
								}
							},
						});
				});
			}
		}
		if (companyUsersArray.length == 0 && authenticatedUser.is_admin) {
			getAllUsers()
				.pipe(take(1))
				.subscribe({});
		}

	}, []);

	const retriveStripePaymentPlansAndSubscriptions = () =>
		getStripePlans()
			.pipe(first())
			.subscribe({
				next: data => {
					setProducts(data.product);
					setPrices(data.prices);
					const defaultPriceForSubscription = data?.product?.find(pr => pr.name === 'User Subscription');
					if (defaultPriceForSubscription) {
						let newSubscriptionMetaData = [];
						Object.entries(defaultPriceForSubscription.metadata).map(item => {
							item[1] = JSON.parse(item[1]);
							newSubscriptionMetaData.push(item);
						});
						newSubscriptionMetaData = Object.fromEntries(newSubscriptionMetaData);
						getCurrentSubscription()
							.pipe(first())
							.subscribe({
								next: data => {
									setServicesPurchased(data.services);
									if (data.services != undefined) {
										if (data.services.length > 0) {
											const date_temp = new Date(
												Math.max(
													...data.services.map(element => {
														return new Date(element.ends_at);
													})
												)
											);
											if (date_temp > now()) {
												setLatestDatePurchased(date_temp);
											}
										}
									}

									if (data.subscription !== undefined) {
										const startDate = new Date(data.subscription.start_date);
										const endDate = new Date(startDate.setMonth(startDate.getMonth() + 1));
										const count = companyData.billable_users ?? 0;
										Object.entries(newSubscriptionMetaData).map(item => {
											if (count <= item[1].max && count >= item[1].min) {
												var formatter = new Intl.NumberFormat('en-US', {
													style: 'currency',
													currency: item[1].currency,
												});
												setSubscriptionInfo({
													...data.subscription,
													end_date: endDate.toDateString(),
													plan: item[0],
													plan_price: `${item[1].currency} ${formatter.format(item[1].price)}`,
													price_code: defaultPriceForSubscription.default_price,
													total: `${item[1].currency} ${formatter.format(item[1].price * count)}`,
												});
												onSetCompanyDataField({ billing_info: data.billing_info });
											}
										});
									} else if (data.message === 'Company still on trial') {
										const count = companyData.billable_users ?? 0;
										Object.entries(newSubscriptionMetaData).map(item => {
											if (count <= item[1].max && count >= item[1].min) {
												var formatter = new Intl.NumberFormat('en-US', {
													style: 'currency',
													currency: item[1].currency,
												});
												setSubscriptionInfo({
													plan: item[0],
													plan_price: `${item[1].currency} ${formatter.format(item[1].price)}`,
													price_code: defaultPriceForSubscription.default_price,
													total: `${item[1].currency} ${formatter.format(item[1].price * count)}`,
												});
											}
										});
									}
								},
								error: () => { },
							});
					}
				},
				error: () => { },
			});

	const parseInvoices = useCallback(invoices => {
		const parsedInvoices = [];

		if (invoices?.length === 0) return;
		invoices.map(elem => {
			return parsedInvoices.push({
				description: elem.description,
				total: elem?.invoice_total,
				status: elem.status,
				date: moment(elem.invoice_date).format('MMMM Do, YYYY'),
				url: (
					<a href={elem.invoice_url} target="_blank" rel="noreferrer">
						View Invoice
					</a>
				),
			});
		});
		setInvoices(parsedInvoices);
	}, []);

	const parsePurchases = useCallback(purchases => {
		const parsedInvoices = [];

		if (purchases?.length === 0) return;
		purchases.map(elem => {
			return parsedInvoices.push({
				description: elem.stripe_product,
				total: (+elem?.stripe_price).toFixed(2),
				status: elem.stripe_status,
				date: moment(elem.starts_at).format('MMMM Do, YYYY'),
				url: '',
				id: `${elem.stripe_id}`.substring(0, 13),
			});
		});
		setInvoices(parsedInvoices);
	}, []);

	const retriveInvoices = () =>
		getInvoices()
			.pipe(first())
			.subscribe({
				next: data => {
					data.invoices && parseInvoices(data.invoices);
				},
				error: () => { },
			});

	const retrivePurchases = () =>
		getPurchases()
			.pipe(first())
			.subscribe({
				next: data => {
					data.data && parsePurchases(data.data);
				},
				error: () => { },
			});

	useEffect(() => {
		PAYMONGO_KEY === '' && retriveStripePaymentPlansAndSubscriptions();
		PAYMONGO_KEY === '' ? retriveInvoices() : retrivePurchases();
		dispatch(setShowMenu(true));
		dispatch(setShowSideMenu(false));
	}, []);

	const payServices = (service_name, price) => {
		navigate('/checkout', {
			state: {
				service_name: service_name,
				price: price,
			},
		});
	};
	const redirectToCheckout = async ({ price }) => {
		const item = {
			price: price,
			quantity: 1,
		};

		const checkoutOptions = {
			lineItems: [item],
			mode: 'payment',
			billingAddressCollection: 'required',
			customerEmail: companyData.billing_info.email,
			clientReferenceId: companyData.billing_info.id,
			successUrl: `${window.location.origin}/success`,
			cancelUrl: `${window.location.origin}/cancel`,
		};
		const stripe = await getStripe();

		setLoading(true);

		const { error } = await stripe.redirectToCheckout(checkoutOptions);

		if (error) setStripeError(error.message);
		setLoading(false);
	};

	const renderPlanSubscriptions = (subs, name) => {
		return (
			Object.keys(subs).length > 0 &&
			Object.keys(subs).map((type, index) => {
				const subsJ = JSON.parse(subs[type]);
				const disabled = servicesPurchased.length == 0 ? false : servicesPurchased.filter(sp => new Date(sp.ends_at).getTime() > now() && sp.stripe_price === subsJ.price_code && sp.stripe_product === name).length > 0;
				return (
					// eslint-disable-next-line react/jsx-key
					<Button disabled={disabled} key={index} className={cls.planButton} lighter style={{ marginBottom: '4px' }} loading={loading} title={type === 'Basic' ? 'Purchase Now' : `Subscribe for ` + type} clicked={() => payServices(name, subsJ)} />
				);
			})
		);
	};

	const columns = React.useMemo(
		() => [
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px', color: 'white' }} alt={''} />
							Payment ID
						</span>
					);
				},
				accessor: 'id',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px', color: 'white' }} alt={''} />
							Invoice Date
						</span>
					);
				},
				accessor: 'date',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Description
						</span>
					);
				},
				accessor: 'description',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Notes
						</span>
					);
				},
				accessor: 'status',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Invoice Total
						</span>
					);
				},
				accessor: 'total',
			},

			// {
			// 	Header: () => {
			// 		return (
			// 			<span>
			// 				<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
			// 				Invoice
			// 			</span>
			// 		);
			// 	},
			// 	accessor: 'url',
			// },
		],
		[]
	);

	const userColumns = React.useMemo(
		() => [
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Username
						</span>
					);
				},
				accessor: 'username',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px', color: 'white' }} alt={''} />
							First Name
						</span>
					);
				},
				accessor: 'first_name',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Last Name
						</span>
					);
				},
				accessor: 'last_name',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Email
						</span>
					);
				},
				accessor: 'email',
			},
			{
				Header: () => {
					return (
						<span>
							<img style={{ marginRight: '6px', marginBottom: '-2px' }} alt={''} />
							Validity
						</span>
					);
				},
				accessor: 'validity',
			},
		],
		[]
	);
	const renderInvoices = () => {
		return (
			<Fragment>
				<Table columns={columns} data={invoices} />
				{invoices.length === 0 && <p className={cls.noPaymentInfo}>{'No available invoices.'}</p>}
			</Fragment>
		);
	};

	const renderPlans = () => {
		return (billProducts.length > 0 || products.length) > 0 && PAYMONGO_KEY === '' ? (
			products.slice(0, 2).map((product, idx) => {
				const price = prices.find(p => p.id === product.default_price) ?? { cost: '0.00', currency: '' };
				return (
					<div key={idx} className={cls.additionalPlanContainer} style={{ marginRight: '20px' }}>
						<p className={cls.planTitle}>{`${product.name} ${product.options.currency}${product.options.price}.00`}</p>
						<p className={cls.planPrice}>
							{price.currency.toUpperCase()} {price.cost} <span className={cls.month}>/month</span>
						</p>
						<div className={cls.planDescriptionContainer}>
							<p className={cls.planDescription} style={{ fontSize: '16px' }}>
								{product.description}
							</p>
						</div>
						{Object.keys(product.metadata).length > 0
							? companyData.status === 'active' && renderPlanSubscriptions(product.metadata, product.id)
							: companyData.status === 'active' && <Button loading={loading} lighter className={cls.planButton} clicked={() => redirectToCheckout({ price: product.default_price })} title={'Purchase Now'} />}
						<br />
					</div>
				);
			})
		) : (
			<div className={cls.plansHost}>
				{billProducts?.map((product, index) => (
					<div key={index} style={{ padding: '10px' }}>
						<UserCard
							//noPhoto={true}

							//photo={product?.options?.photo}
							height={'340px'}
							firstName={`${product.stripe_ref}`}
							position={`${product.options.currency} ${product.options.price} ${product.options.dlabel}`}
							email={product.options.description}
							cardClicked={() => {
								if (billableUsers.length > 0) {
									setSubscriptionInfo({
										plan_price: product.options.price,
										currency: product.options.currency,
										plan: product.name ?? product.stripe_ref,
										total: product.options.price * (customBillableUsers.length == 0 ? billableUsers.length : customBillableUsers.length),
										billable_users: customBillableUsers.length == 0 ? billableUsers : customBillableUsers,
										duration: product.options.duration,
										dlabel: product.options.dlabel,
									});

									dispatch(setShowMenu(false));
									navigate('/checkout', {
										state: {
											companyData: companyData,
											subscriptionInfo: {
												plan_price: product.options.price,
												currency: product.options.currency,
												plan: product.name ?? product.stripe_ref,
												total: product.options.price * (customBillableUsers.length == 0 ? billableUsers.length : customBillableUsers.length),
												billable_users: customBillableUsers.length == 0 ? billableUsers : customBillableUsers,
												duration: product.options.duration,
												dlabel: product.options.dlabel,
											},
											type: 'Subscription',
										},
									});
								}
							}}
						></UserCard>
					</div>
				))}
				<div style={{ padding: '10px' }}>
					<UserCard
						//noPhoto={true}
						//photo={'https://static.thenounproject.com/png/3203384-200.png'}
						height={'340px'}
						firstName={`Enterprise`}
						position={`Custom`}
						email={`Feel free to contact us to discuss your large scale needs, self-hosted requirements or application customization. Click this button to send an email to support. We will immediately get back to you.`}
						cardClicked={() => {
							window.location = 'mailto:manuel.parungao@adpgxsoftware.com';
						}}
					></UserCard>
				</div>
			</div>
		);
	};

	const navigate = useNavigate();
	return (
		<div className={cls.billingInfoHost} style={{ margin: '200px 0 0 0', padding: '0 0 0 10%' }}>
			<HeaderTopBar headerTitle={'Billing'} targetProcessing={false} />
			<PermissionWrapper permissions={[po.billing.view]}>
				<div style={{ display: 'flex' }}>
					<div className={cls.currentInvoicesContainer} style={{ minWidth: '200px', display: companyData.status === 'on_trial' || companyData.status === 'trial_ended' || companyData.status === 'inactive' ? '' : 'none' }}>
						<p className={cls.dashboardTitle}>Active Plan</p>
						<p className={cls.trialText} style={{ fontWeight: '100', fontSize: companyData.status === 'inactive' ? '16px' : '20px', textAlign: 'center' }}>
							{companyData.status === 'on_trial' ? `Trial (${parseInt((new Date(companyData.trial_ends_at).getTime() - now()) / (1000 * 3600 * 24))} days left)` : companyData.status === 'inactive' ? 'Expired. Subscription required' : 'Trial Ended'}
						</p>
					</div>
					<div className={cls.currentInvoicesContainer} style={{ minWidth: '200px' }}>
						<p className={cls.dashboardTitle}>Total Users</p>
						<p className={cls.trialText} style={{ fontWeight: '100', textAlign: 'center' }}>
							{companyUsersArray?.length ?? ''}
						</p>
					</div>
					<div className={cls.currentInvoicesContainer} style={{ minWidth: '200px', display: companyData.status === 'on_trial' || companyData.status === 'trial_ended' ? 'none' : '' }}>
						<p className={cls.dashboardTitle}>Subscribed Users</p>
						<p className={cls.trialText} style={{ fontWeight: '100', textAlign: 'center' }}>
							{(companyUsersArray?.length ?? 0) - billableUsers.length}
						</p>
					</div>
					<div className={cls.currentInvoicesContainer} style={{ minWidth: '200px' }}>
						<p className={cls.dashboardTitle}>For Subscription</p>
						<p className={cls.trialText} style={{ fontWeight: '100', textAlign: 'center' }}>
							{customBillableUsers?.length > 0 ? customBillableUsers.length : billableUsers.length}
						</p>
					</div>
				</div>

				<Tabs
					style={{ fontFamily: 'var(--app-text-main-font)', fontSize: isMobile ? '12px' : '20px' }}
					selectedIndex={tabIndex}
					onSelect={index => {
						setTabIndex(index);
					}}
				>
					<TabList>
						<Tab>{'Products'}</Tab>
						<Tab>{'Subscriptions'}</Tab>
					</TabList>

					<TabPanel>
						<div style={{ minWidth: isMobile ? '110%' : '600px', marginTop: '20px', width: isMobile ? '100%' : '80%' }}>
							{`Click one plan to subscribe:`}

							<div style={{ display: 'flex' }}>
								{renderPlans()}
							</div>

							{'Optional: Select users for individual subscription.'}

							<div className={cls.userPanelHost}>
								{billableUsers.map((user, index) => {
									return (
										<div className={cls.userPanelRows} key={index}>
											<TaskCard
												task={{ name: showAll ? `${user.first_name} ${user.last_name} (${user.validity ?? 'New'})` : `${user.first_name} ${user.last_name}`, status: null }}
												clicked={() => {
													if (customBillableUsers.filter(usr => usr.id === user.id).length > 0) {
														setCustomBillableUsers(customBillableUsers.filter(usr => usr.id !== user.id));
													} else {
														setCustomBillableUsers([...customBillableUsers, user]);
													}
												}}
												setTaskToEdit={() => { }}
												openModal={() => { }}
											/>
										</div>
									);
								})}
								{billableUsers.length != companyUsersArray.length && (
									<div className={cls.userPanelRows}>
										<TaskCard
											task={{ name: `Show all instead`, status: null }}
											clicked={() => {
												if (showAll == true) {
													setBillableUsers(companyUsersArray);
												} else {
													setCustomBillableUsers([]);
													setBillableUsers(companyUsersArray.filter(user => new Date(user.validity) < now()));
												}

												setShowAll(!showAll);
											}}
											setTaskToEdit={() => { }}
											openModal={() => { }}
										/>
									</div>
								)}
							</div>
						</div>
					</TabPanel>
					<TabPanel>
						<div style={{ minWidth: isMobile ? '110%' : '600px', marginTop: '20px', width: isMobile ? '100%' : '70%' }}>
							<div style={{ display: PAYMONGO_KEY === '' ? 'none' : 'flex' }}>
								<Table columns={userColumns} data={companyUsersArray} />
							</div>
						</div>
					</TabPanel>

					<br />
				</Tabs>
			</PermissionWrapper>

			<div className={cls.wallet} >
				<div style={{ color: 'black', fontSize: '14px', }}>{`Payments Wallet:`}</div>
				<div style={{ color: 'black', fontSize: '20px', fontWeight: 'bold', margin: '5px 0 80px' }}>Php {authenticatedUser?.options?.credit?.toFixed(2) ?? '0.00'}</div>
				<div className={cls.actionsContainer}>
					<Button clear title={'Top Up'} clicked={() => setAmount(0)} style={{ backgroundColor: 'var(--app-color-blue)', color: 'white', width: '140px', display: amount > -1 ? 'none' : '' }} />
				</div>
				<div style={{ width: 'inherit', display: amount === -1 ? 'none' : '' }}>
					<div style={{ color: 'black' }} onClick={() => setAmount(-1)}>{'Select your denomination:'}</div>

					<Button light title={'Php 500.00'} clicked={() => setAmount(500)} style={{ backgroundColor: amount === 500 ? 'var(--app-color-gray-light)' : 'white', width: '100%' }} />
					<Button light title={'Php 1000.00'} clicked={() => setAmount(1000)} style={{ backgroundColor: amount === 1000 ? 'var(--app-color-gray-light)' : 'white', width: '100%' }} />
					<Button light title={'Php 2000.00'} clicked={() => setAmount(2000)} style={{ backgroundColor: amount === 2000 ? 'var(--app-color-gray-light)' : 'white', width: '100%' }} />
					<Button light title={'Php 3000.00'} clicked={() => setAmount(3000)} style={{ backgroundColor: amount === 3000 ? 'var(--app-color-gray-light)' : 'white', width: '100%' }} />
					{amount > 0 && <Button light title={'Proceed to Checkout'} clicked={() => {
						dispatch(setShowMenu(false));
						navigate('/checkout', {
							state: {
								companyData: companyData,
								subscriptionInfo: {
									user_id: authenticatedUser.id,
									ref: authenticatedUser.id,
									plan_price: amount,
									currency: 'PHP',
									plan: `Credits`,
									total: amount,
									billable_users: [authenticatedUser],
									duration: 0,
									dlabel: 'Flowhero',
									id: generateNewUUID(),
								},
								type: 'Credits',
							},
						});
					}
					} style={{ backgroundColor: 'var(--app-color-blue)', color: 'white', width: '100%' }} />}
				</div>
			</div>

			<Tabs
				style={{ fontFamily: 'var(--app-text-main-font)', fontSize: isMobile ? '12px' : '20px' }}
				selectedIndex={tabIndex}
				onSelect={index => {
					setTabIndex(index);
				}}
			>
				<TabList>
					<Tab>{'Payment History'}</Tab>
				</TabList>

				<TabPanel>
					<div style={{ fontSize: '18px', minWidth: isMobile ? '110%' : '600px', marginTop: '20px', width: isMobile ? '100%' : '70%' }}>{renderInvoices()}</div>
				</TabPanel>
			</Tabs>
			{apiError?.length > 0 && <p className={cls.errorParagraph}>{apiError}</p>}
			{stripeError && <MessageContainer message={stripeError} />}
			{subscriptionDone && (
				<RenderCompletedModal
					message={''}
					handler={data => {
						dispatch(setBillable({}));
						setCustomBillableUsers([]);
						setBillableUsers(data.filter(user => new Date(user.validity) < now()));
						navigate('/billing-info');
						setSubscriptionDone(false);
					}}
				/>
			)}
			{companyData.status === 'active' && subscriptionInfo.price_code !== undefined && (
				<Button
					className={cls.logoutButton}
					title={'Cancel Subscription'}
					clicked={() => {
						setShowCancelMembership(true);
					}}
				/>
			)}
			{
				<ModalR
					blured={true}
					scrollable={true}
					isOpen={showCancelMembership}
					onClose={() => {
						setShowCancelMembership(false);
					}}
					backdropColor={true}
					shadowedContainerStyle={{ width: '680px' }}
				>
					<CancelMembership
						onCancelMembership={reasons => {
							setShowCancelMembership(false);
							unsubscribe(reasons)
								.pipe(first())
								.subscribe({
									next: () => {
										getCompanyDetails().pipe(first()).subscribe({});
									},
									error: error => {
										setStripeError(error.data.message.message);
									},
								});
						}}
						onClose={() => {
							setShowCancelMembership(false);
						}}
					/>
				</ModalR>
			}
		</div>
	);
}

function Table({ columns, data, cl = cls.tasksTableHost }) {
	const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({
		columns,
		data,
	});

	return (
		<div className={cl}>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map(headerGroup => (
						// eslint-disable-next-line react/jsx-key
						<tr {...headerGroup.getHeaderGroupProps()}>
							{headerGroup.headers.map(column => (
								// eslint-disable-next-line react/jsx-key
								<th {...column.getHeaderProps()}>{column.render('Header')}</th>
							))}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{rows.map(
						(row, index) =>
							prepareRow(row) || (
								<tr key={index} {...row.getRowProps()}>
									{row.cells.map(cell => {
										// eslint-disable-next-line react/jsx-key
										return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>;
									})}
								</tr>
							)
					)}
				</tbody>
			</table>
		</div>
	);
}

const mapStateToProps = store => {
	const {
		authenticatedUser: { position },
		authenticatedUser,
	} = store.authReducer;
	const { companyData, companyPositionsArray, companyUsersArray, billable, billProducts } = store.flowReducer;
	return { companyData, companyPositionsArray, position, companyUsersArray, billable, billProducts, authenticatedUser };
};

const mapDispatchToProps = dispatch => {
	return {
		onSetCompanyDataField: field => dispatch(setCompanyData(field)),
		onSetAuthenticatedUserPosition: position => dispatch(setAuthenticatedUser(position)),
		onTutorialModalOpen: options => dispatch(tutorialModalOpen(options)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(BillingInfo);

const RenderCompletedModal = props => {
	const { setCompletedModal, message, handler = () => { } } = props;
	const [loading, setLoading] = useState(false);

	return (
		<ModalR isOpen={true} shadowedContainerStyle={{ width: '35%', minWidth: '500px', minHeight: '400px' }} onClose={() => setCompletedModal(false)} scrollable={true} blured={true}>
			<div
				style={{
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'center',
				}}
			>
				<p
					style={{
						marginTop: '50px',
						fontSize: '26px',
						color: '#4A4A4A',
						marginBottom: '5px',
						textAlign: 'center',
					}}
				>
					{`Your subscription to ${APP_NAME} was successful`}
				</p>
				<p
					style={{
						width: '300px',
						fontFamily: 'var(--app-text-main-font)',
						fontSize: '16px',
						color: '#4A4A4A',
						textAlign: 'center',
						marginBottom: '20px',
					}}
				>
					<br />
					{'Newly subcribed user may need to login again to refresh their page.'}

					<br />
				</p>
				<Button
					style={{ width: '200px' }}
					loading={loading}
					clicked={() => {
						setLoading(true);
						getAllUsers(true)
							.pipe(take(1))
							.subscribe({
								next: data => handler(data),
							});
					}}
					title={'Continue'}
				/>
			</div>
		</ModalR>
	);
};
